<template>
<div class = "autocomplete-container">
  <input
    @keydown.stop = "hitEnter"
    type = "text"
    @focus = "showSuggestions = true"
    @blur = "showSuggestions = false"
    v-model = "typedText"
    :placeholder = "placeholder"/>
  <transition name = "fade">
    <div v-if = "showSuggestions" class = "autocomplete-suggestion-container">
      <div
        @mousedown="selectSlice(item)"
        class = "autocomplete-suggestions"
        v-bind:key = "index"
        v-for = "(item,index) in filteredArray()">
        {{ item }}
      </div>
    </div>
  </transition>
</div>
</template>

<script>
export default {
  props: {
    placeholder: {
      type: String,
      default: 'Start typing to search ...'
    },
    rawarray: {
      type: Array,
      default: () => ([])
    }
  },
  data: () => ({
    showSuggestions: false,
    typedText: ''
  }),
  methods: {
    filteredArray: function () {
      return this.rawarray.filter(item => {
        const regex = new RegExp(this.typedText, 'gi')
        return regex.test(item)
      })
    },
    selectSlice: function (item) {
      this.$emit('selectslice', item)
    },
    hitEnter: function () {
      if (event.key === 'Enter') {
        const array = this.filteredArray()
        if (array.length > 0) {
          this.selectSlice(array[0])
          this.typedText = ''
          const el = document.querySelector(':focus')
          if (el) el.blur()
        }
      }
    }
  }
}
</script>

<style scoped>
div.autocomplete-container
{
  cursor:default;
}

.autocomplete-suggestions
{
  width:100%;
  display:block;
  padding:0.1em 0.3em;
  transition: all 200ms;
  background-color:rgba(200,200,200,0.8);
}

.autocomplete-suggestions:hover
{
  background-color:rgba(150,150,150,0.8);
}

div.autocomplete-container:focus-within .autocomplete-suggestions
{
  display: block;
}

div.autocomplete-suggestion-container
{
  width: 100%;
  max-height:10em;
  overflow-y:auto;
  overflow-x:hidden;
}
</style>



